.container-countdown {
height: 35vh;
display: grid;
align-items: center;
}

.countdown {
display: grid;
grid-template-columns: repeat(auto-fit, minmax(240px, 240px));
justify-content: center;
gap: 10px;
padding: 10px;
}

.countdown li {
font-family: Georgia, "Times New Roman", Times, serif;
font-style: italic;
padding: 1rem;
color: #fff;
background: #076007;
border-radius: 5px;
box-shadow: 0 2px 2px rgba(0, 0, 0, 0.4);
text-align: center;
list-style: none;
}

.countdown li span {
font-family: Arial, Helvetica, sans-serif;
font-style: normal;
font-size: 4em;
}